





























    export default {
        data() {
            return {
                uid: 0,
                profit_price: 0,
                total_price: 0,
				start_time:'',
				end_time:'',
				date:'',
            }
        },
        onLoad() {
            this.uid = this.common.get("uid")
			// 库存管理接口
            this.listInterface()
        },
		
        methods: {
			searchTime() {
				this.date = this.start_time + ' ' +'-'+ ' ' + this.end_time
				this.listInterface()
			},
			// 库存管理接口
			listInterface() {
				this.http({
				    url: this.api.Statistic_index,
				    method: "POST",
				    data: {
				        uid: this.uid,
						date:this.date
				    },
				    success: res => {
				        this.profit_price = res.profit_price
				        this.total_price = res.total_price
				    }
				})
			},
			// 返回
			navigateBack() {
				uni.switchTab({
					url:"/pages/index/index"
				})
			}
        },
    }
